.back-to-top {
  display: none;
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  background: none;
  cursor: pointer;
}
.show_back-to-top {
  display: block;
}
.scroll-icon {
  font-size: 2rem;
  color: #808080;
  transition: all 0.3s linear;
}

.scroll-icon:hover {
  font-size: 2.5rem;
}
